/* src/Index.css */

body, html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
  }
  
  .index-page {
    position: relative;
    width: 100%;
    height: 100vh; /* Ensure it covers the full viewport height */
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #2e2e2e;
  }
  
  .content {
    position: absolute;
    top: 20%; /* Adjust this value to position it higher or lower */
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #fff;
    z-index: 1;
    width: 100%;
  }
  
  .content h1,
  .content p {
    margin: 20px 0;
  }
  
  .content button {
    margin: 10px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    background-color: #202020; /* Dark grey button background */
    color: #fff;
    border: none;
    border-radius: 4px;
  }
  
  .content button:hover {
    background-color: #474747; /* Slightly darker grey on hover */
  }
  